@use'common';

.MainVisual {
  position: relative; height: calc(100vh - 130px);

  .inner {
    padding: 140px 0; font-size: 120px; line-height: 1; color: black; font-family: 'poppins';

    .top {
      display: flex; justify-content: space-between; margin: 0 0 30px 0; font-weight: 600 ;

      span { font-weight: 700; }
      img { height: 150px; }
    }
    .mid {
      display: flex; justify-content: space-between; margin: 0 0 30px 0;

      strong { font-weight: 800; }
      img { height: 150px; }
    }
    .bot {
      display: flex; justify-content: space-between;

      strong { font-weight: 800; }
      img { height: 150px; }
    }
  }
  .button {
    position: absolute; inset: 650px auto auto 50%; transform: translate(-50%,-50%); margin: 0 0 0 -450px;

    img { animation: rotate 6s linear infinite; transform-origin: 50% 50%; width: 140px; }
    @keyframes rotate {
      100% { transform: rotate(360deg); }
    }
  }
  .cir02{  position: absolute; inset: 650px auto auto 50%; transform: translate(-50%,-50%); margin: 0 0 0 -450px; width: 140px;}
}